.galeria {
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .galeria img {
    width: 8.5rem;
    height: 8.5rem;
    margin: 1rem; }
    @media screen and (min-width: 425px) {
      .galeria img {
        width: 10rem;
        height: 10rem; } }
    @media screen and (min-width: 768px) {
      .galeria img {
        width: 13rem;
        height: 13rem; } }
    @media screen and (min-width: 1024px) {
      .galeria img {
        width: 18rem;
        height: 18rem; } }
    @media screen and (min-width: 1366px) {
      .galeria img {
        width: 25rem;
        height: 25rem; } }
  .galeria .wrapper-modal {
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    color: #333333; }
    .galeria .wrapper-modal .modal {
      background-color: rgba(0, 0, 0, 0.7);
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative; }
      .galeria .wrapper-modal .modal img {
        width: 90%;
        height: auto; }
        @media screen and (min-width: 768px) {
          .galeria .wrapper-modal .modal img {
            width: auto; } }
      .galeria .wrapper-modal .modal .close {
        color: #fff;
        display: block;
        font-family: Verdana;
        font-size: 22px;
        height: 25px;
        text-decoration: none; }
