.infos {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: solid 1px #50f;
  -moz-box-shadow: 0 -3px 8px 4px #50f;
  -webkit-box-shadow: 0 -3px 8px 4px #50f;
  box-shadow: 0 -3px 8px 4px #50f; }
  @media screen and (min-width: 1024px) {
    .infos .idwall {
      display: flex;
      align-items: center; } }
  @media screen and (min-width: 1024px) {
    .infos .myinfos {
      display: flex;
      align-items: center; } }
  .infos .myinfos p:first-child {
    margin-right: 1rem; }
  .infos .myinfos p strong {
    font-family: MargemBold, sans-serif; }
